'use scrict';

export default {
	data() {
		return {
			oresult: "",
			mresult: "",
			run: "未匹配",
			socketOpen: false,
			id: parseInt(Math.random()*1000),
			to: "",
			toReady: false
		}
	},
	onShow: function () {
		uni.connectSocket({
			url: "wss://itnewdata.com:12002/websocket/"+this.id
		});
		uni.onSocketOpen(res => {
			console.log("已接建立连接");
			this.socketOpen = true;
		});
		uni.onSocketMessage(response => {
			let res = JSON.parse(response.data);
			console.log("接收", res);
			if (res.code == 101) {
				this.to = res.data;
				this.run = "已连接";
			} else if (res.code == 102) {
				this.toReady = true;
				this.run === "准备" && (this.run = "开始");
			} else if (res.code == 103) {
				this.oresult = res.url;
			}
		});
	},
	onHide: function () {
		uni.onSocketClose(res => {
			console.log(res);
		});
	},
	methods: {
		send: function (data) {
			console.log("发送", data);
			this.socketOpen && uni.sendSocketMessage({
				data: JSON.stringify(data)
			});
		},
		shot: function (e) {
			const tab = e.target.dataset.tab;
			if (tab === "fist") {
				this.mresult = "../../static/img/fist.png";
			} else if (tab === "shears") {
				this.mresult = "../../static/img/shears.png";
			} else if (tab === "cloth") {
				this.mresult = "../../static/img/cloth.png";
			}
		},
		start: function () {
			if (this.run === "已连接") {
				this.run = this.toReady ? "开始" : "准备";
				this.send({
					to: this.to,
					result: {
						code: 102
					}
				});
			} else if (this.run === "开始") {
				if (!this.mresult) {
					this.$toast({
						title: "请选择出拳|剪|布",
						icon: "none"
					});
					return;
				}
				this.run = 3;
				let time = setInterval(() => {
					 if (this.run <= 0) {
						clearInterval(time);
						this.send({
							to: this.to,
							result: {
								code: 103,
								url: this.mresult
							}
						});
						this.toReady = false;
					} else {
						this.run--;
					}
				}, 1000);
			} else if (this.run === 0) {
				this.run = "已连接";
				this.oresult = "";
				this.mresult = "";
			}
		}
	}
}
